/* ==========================================================================
   Footer responsive style - begin
   ========================================================================== */

@include ipad-landscape {
    
    .eltdf-page-footer {

		.eltdf-footer-top-holder {

			.eltdf-footer-top-inner {

				.eltdf-column-content {
					margin: 0 0 50px;
					min-height: inherit !important; /* important to override inline style added from script */

					&:after {
						display: none;
					}

					&:last-child {
						margin: 0;
					}

					@for $i from 0 to 4 {
						&.eltdf-footer-column-#{$i} {
							text-align: center !important; /* important to override inline style */
						}
					}

					@for $i from 0 to 4 {
						.eltdf-footer-column-#{$i} {
							text-align: center !important; /* important to override inline style */
						}
					}

					@media only screen and (min-width: $ipad-portrait-plus-pixel) {

						&.eltdf-grid-col-3 {
							width: 50%;
							float: left;

							&:nth-child(2n+1) {
								clear: both;
							}
						}

						&.eltdf-grid-col-4 {
							width: 100%;
							float: left;
						}
					}
				}

				.eltdf-separator-disable-responsive {
					display: none;
				}

				.eltdf-separator-holder {
					text-align: center;
				}
			}
		}
	}
}

@include ipad-portrait {

    .eltdf-page-footer {

		.eltdf-footer-top-holder {

			.eltdf-footer-top-inner {

				&.eltdf-full-width{
					padding: 53px 20px;
				}

				.eltdf-column-content {
					min-height: 0 !important; /* important to override inline js */

					&:not(:first-child) {
						margin-top: 20px;
					}

					&:after {
						border: 0;
					}
				}
			}
		}
    }
}

@include phone-portrait {

	.eltdf-page-footer {

		.eltdf-footer-bottom-holder {

			.eltdf-footer-bottom-inner {

				&.eltdf-full-width {
					padding: 14px 20px;
				}
			}
		}
	}
}


/* ==========================================================================
   Footer responsive style - end
   ========================================================================== */